body {
  height: 100vh;
  background-color: #f0f0f0;
  margin: 0;
  font-family: Arial, sans-serif;
  position: relative;
}

p {
  margin: 0;
  position: absolute;
  top: 10px;
  left: 10px;
}

p a {
  color: #fff;
  background-color: #2196f3;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  text-decoration: none;
}

.startstop {
  flex-direction: row;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

button {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

button[data-start] {
  color: #fff;
  background-color: #4caf50;
}

button[data-stop] {
  color: #fff;
  background-color: #fa9e14;
}

button[data-start]:disabled, button[data-stop]:disabled {
  background-color: #9e9e9e;
}

